*{box-sizing:border-box;margin:0;padding:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.header{background:linear-gradient(90deg, #6e5efe 0%, #493ffc 100%);height:80px;display:flex;justify-content:center;align-items:center;font-size:1.2rem}.header .logo{color:#fff;justify-self:start;margin-left:20px;cursor:pointer}.header svg{margin-left:0.5rem;font-size:1.6rem}.header .menu{display:grid;grid-template-columns:repeat(5, auto);grid-gap:10px;list-style:none;text-align:center;width:70vw;justify-content:end;margin-right:2rem}.header .menu a{color:#fff;text-decoration:none;padding:0.5rem 1rem}.header .menu a:hover{background-color:#6d76f7;border-radius:4px;transition:all 0.2s ease-out}

